import { Link } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { GoogleOAuthProvider, GoogleLogin, useGoogleLogin, useGoogleOneTapLogin, TokenResponse } from '@react-oauth/google';
import axios from 'axios';
import { OAuth2Client } from 'google-auth-library';

const AuthThirdParty = () => {
  const { t, i18n } = useTranslation();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;

  //function onSuccess() {
  //  setFormWithdraw(e.target.value);
  //}

  const googleLogin = useGoogleLogin({
    onSuccess: async (tokenResponse) => {
      console.log(tokenResponse);
      const userInfo = await axios.get(
        'https://www.googleapis.com/oauth2/v3/userinfo',
        { headers: { Authorization: 'Bearer <tokenResponse.access_token>' } },
      );
      console.log(userInfo);
    },
    onError: errorResponse => console.log(errorResponse),
  });


  return (
    <>
    <div className="container">
      <div className="mt-3 list row">
        <div className="col-md-12">
          <p className="mt-3 mb-3 page-navigator">
            <Link to={langUrl}>{t('home.title')}</Link> / <Link to={langUrl + "/fullstack"}>{t('full_stack.title')}</Link> / <Link to={langUrl + "/fullstack/auth-signin-with-thirdparty"}>Sign In with Third Party Platform</Link>
          </p>
          <h4 className="text-center">Sign In with Third Party Platform</h4>
        </div>
      </div>
      <div className="mt-3 mb-3 list row">
        <div className="col-md-12 text-center d-flex justify-content-center">
            <button onClick={() => googleLogin()}>
              Sign in with Google {' '}
            </button>
            {/*}
            <GoogleLogin
              onSuccess={credentialResponse => {
                console.log(credentialResponse);
                const oAuth2Client = new OAuth2Client(
                  '669682397688-3521865rjs5mqln8ac6ujh7mltfodqbn.apps.googleusercontent.com',
                  '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',
                  'http://localhost:3000'
                );


                
              }}
              onError={() => {
                console.log('Login Failed');
              }}
            useOneTap />*/}
        </div>
      </div>
    </div>
    </>
  );
}

export default AuthThirdParty
